<template>
	<div>
		<div class="nickname-header">
			<div class="nickname-zhong">
				<div class="nickname-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="nickname-ti">修改昵称</div>
				<div></div>
			</div>
		</div>
		<div class="nickname-down">
			<div class="nickname-nicheng">
				<!--<input type="text" name="" placeholder="昵称">-->
				<input type="text" v-model="data.nickname">
			</div>
		</div>
		<div class="nickname-baocun" @click="updt_user">保存</div>
	</div>
</template>
<script>
	import Qs from "qs"
	export default {
		name: 'HelloWorld',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App',
				data: {},
				open:false,
				user_info: {}
			}
		},
		mounted(){},
		methods: {
			back() {
				window.history.back();
			},
			updt_user() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					nickname: that.data.nickname
				};
				that.$http.post('/user/updt_user', Qs.stringify(params), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					if(res.meta.code == 200) {
						this.$alert('修改成功', '提示');
//						this.userInfo();
						
					} else {
						this.$alert(res.meta.msg, '提示');
					}
				})
			},
			//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if (!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
				this.data.nickname = this.user_info.user_nickname;
			}
			this.userInfo();
		}
	}
</script>
<style scoped>
	.nickname-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.nickname-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.nickname-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.nickname-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.nickname-down {
		margin-top: 1.5rem;
	}
	
	.nickname-nicheng {
		border: 1px solid #ffffff;
		border-radius: 10px;
		background: #ffffff;
		width: 95%;
		margin: 0 auto;
	}
	
	.nickname-nicheng input {
		font-size: 1.4rem;
		margin-left: 1.5rem;
		color: #9fa0a0;
		border: none;
		outline: none;
		width: 90%;
		height: 3rem;
	}
	
	.nickname-down {
		margin-top: 3rem;
	}
	
	.nickname-baocun {
		font-size: 1.8rem;
		color: #FFFFFF;
		border: 1px solid #8CADF5;
		border-radius: 5px;
		background: #8CADF5;
		text-align: center;
		padding: 0.7rem 0;
		width: 95%;
		margin: 3rem auto;
	}
</style>


